import React from 'react';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import { NavLink } from 'react-router-dom';

const Common=(props)=>{return(<>
<section id="header" className="">
    <div className="container-fluid ">
        <div className="row">
            <div className="col-10 mx-auto">
                <div className="row d-flex align-items-center">
                    <div className="col-md-6 pt-5 order-2 order-lg-1">
                        <h1>{props.name}<span className="brand-name"> {props.brand}</span></h1>
                        <h2 className='my-3'>
                        we are the team of talented developers making websites
                        </h2>
                        <div className='mt-3'>
                            <NavLink to={props.visit} className="btn btn-outline-primary rounded-pill" >{props.btnname}</NavLink>
                        </div>
                    </div>

                    <div className="col-lg-6 order-1 order-lg-2 header-img">
                        <img src={props.imgsrc} className="img-fluid animated" alt="img_error"/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</>
)}

export default Common;